import * as React from 'react'
import { Link } from 'gatsby'
import {
    navLinkItem,
    navLinkText
} from './navLinkItem.module.css'


const NavLinkItem = ({ to, children }) => {
    return (<li className={navLinkItem}><Link className={navLinkText} to={to}>{children}</Link></li>)
}

export default NavLinkItem