import * as React from 'react'
import { graphql, useStaticQuery } from 'gatsby'

//Todo: https://www.gatsbyjs.com/docs/how-to/adding-common-features/adding-seo-component/ <-- follow this properly - currently just shortcutting and using title and description only

const SEO = ({ title, description, children }) => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          description
        }
      }
    }
  `)

  return (
    <>
      <title>{title} | {data.site.siteMetadata.title}</title>
      <meta name="description" content={description || data.site.siteMetadata.description} />
      {children}
    </>
  )
}

export default SEO